import React from "react";
import { Wrapper, WidthWrapper } from "../../../components/Layout";
import { StyledH2 } from "../../../components/Text";
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import { styled } from "@mui/system";
import { useWindowWidth } from "../../../hooks/useWindowWidth";
import { featuredSponsors, backedSponsors } from "../../../config/constants/sponsors";

const Dotline = styled('div')({
    borderLeft: "dashed 3.5px #b9b6b6",
    height: "170px"
})

const ImgContainer = styled('div')({
    width: "120px",
    height: "120px",
    borderRadius: "50%",
    display: 'flex',
    alignItems: "center",
    justifyContent: "center",
    backgroundColor: "#ffffff",
    boxShadow: "0 5px 2rem rgb(0 0 0 / 5%)",

})

const Sponsor = () => {
    const isShow = useWindowWidth(900)

    return (
        <Wrapper>
            <WidthWrapper>
                <Grid container spacing={2}>
                    <Grid container item sm={12} md={5} alignItems="center" justifyContent="start">
                        <Box>
                            <StyledH2>Featured In</StyledH2>

                            <Grid container spacing={2}>
                                {
                                    featuredSponsors.map(sponsor => (
                                        <Grid container item xs={6} sm={4} alignItems="center" justifyContent="center" key={sponsor.sid}>
                                            <ImgContainer>
                                                <img src={sponsor.image} alt="sposnor" />
                                            </ImgContainer>
                                        </Grid>
                                    ))
                                }
                            </Grid>
                        </Box>
                    </Grid>

                    {
                        isShow && <Grid container item sm={12} md={2} alignItems="center" justifyContent="center"><Dotline /></Grid>
                    }

                    <Grid container item sm={12} md={5} alignItems="center" justifyContent="start">
                        <Box>
                            <StyledH2>Backed By</StyledH2>

                            <Grid container spacing={2}>
                                {
                                    backedSponsors.map(sponsor => (
                                        <Grid container item xs={6} sm={4} alignItems="center" justifyContent="center" key={sponsor.sid}>
                                            <ImgContainer>
                                                <img src={sponsor.image} alt="sposnor" />
                                            </ImgContainer>
                                        </Grid>
                                    ))
                                }
                            </Grid>
                        </Box>
                    </Grid>
                </Grid>
            </WidthWrapper>
        </Wrapper>
    )
}

export default Sponsor